import { Button } from 'components';
import { Field, Form, Formik, ErrorMessage } from 'formik';
import { object, string } from 'yup';

export default function TeacherForm({ addTeacher }) {
  const initialValues = {
    lastName: '',
    firstName: '',
    patronymic: '',
    phone: '',
    email: '',
    city: '',
  };

  const handleSubmitForm = (values, { setSubmitting, resetForm }) => {
    setSubmitting(true);
    addTeacher(values);
    resetForm();
    setSubmitting(false);
  };

  const fieldData = [
    {
      name: 'lastName',
      label: '',
    },
    {
      name: 'firstName',
      label: "'",
    },
    {
      name: 'patronymic',
      label: '',
    },
    {
      name: 'phone',
      label: '',
    },
    {
      name: 'email',
      label: 'e-mail',
    },
    {
      name: 'city',
      label: '',
    },
  ];

  const validationSchemaForm = object().shape({
    lastName: string().required('   '),
    firstName: string()
      .min(2, '  ')
      .required('   '),
    patronymic: string().required('   '),
    phone: string().matches(/^\+[0-9]{3}\s\((\d+)\)\d{7}/, ' +380 (00)0000000'),
    email: string().required(),
    city: string().required(),
  });

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={handleSubmitForm}
      validationSchema={validationSchemaForm}
    >
      {({ values, handleChange, handleBlur }) => (
        <Form>
          <div>
            <h3> </h3>
            {fieldData.map(({ name, label }) => (
              <div key={name}>
                <label htmlFor={name}>{label}</label>
                <Field
                  type="text"
                  name={name}
                  id={name}
                  onBlur={handleBlur}
                  onChange={handleChange}
                  placeholder={label}
                  value={values[name] || ''}
                />
                <ErrorMessage name={name} component={'div'} />
              </div>
            ))}
          </div>
          <Button text="" type="submit" />
        </Form>
      )}
    </Formik>
  );
}
